<template>
    <div class="searchExchange">
        <el-form :model="form" label-position="left" label-width="100px" :inline="true">
            <el-row>
                <el-col :span="24">
                    <el-form-item label="組合">
                        <el-select v-model="form.groupName" placeholder="请選擇組合">
                            <el-option v-for="(member,i) in groups" :label="member"  :value="member" :key="i"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="物品類型">
                        <el-select v-model="form.category" placeholder="请選擇物品類型">
                            <el-option v-for="(category,i) in categories" :label="category"  :value="category" :key="i"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item v-if="form.category === '專卡'">
                        <el-select v-model="form.album" placeholder="请選擇專輯">
                            <el-option v-for="(album,i) in albums" :label="album"  :value="album" :key="i"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="24">
                    <el-form-item label="擁有成員" v-if="form.groupName">
                        <el-checkbox-group v-model="form.ownMember">
                            <el-checkbox v-for="(member,i) in members" :label="member"  :key="i"  name="type"></el-checkbox>
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="欲交換成員" v-if="form.groupName">
                        <el-checkbox-group v-model="form.targetMember">
                            <el-checkbox v-for="(member,i) in members" :label="member"  :key="i"  name="type"></el-checkbox>
                        </el-checkbox-group>
            </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <el-button @click="initForm()">重設</el-button>
        <el-button>搜尋</el-button>
    </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
    data(){
        return {
            form: {
                groupName: '',
                album: '',
                category: '',
                itemType:'',
                exchangeWay: [],
                ownMember:[],
                targetMember: [],
            },
            members:[],
            albums:[],
            categories:['專卡','玩偶/娃娃','官方周邊','飯制周邊'],
            ways: ['店到店','賣貨便','面交','郵寄','其他'],
        }
    },
    computed:{
        ...mapState({
            groups: state => state.basic.groups,
            nctMembers: state => state.basic.nctMembers,
            nct127Members: state => state.basic.nct127Members,
            nctdreamMembers: state => state.basic.nctdreamMembers,
            wayvMembers: state => state.basic.wayvMembers,
            nctAlbums: state => state.basic.nctAlbums,
            nct127Albums: state => state.basic.nct127Albums,
            nctdreamAlbums: state => state.basic.nctdreamAlbums,
            wayvAlbums: state => state.basic.wayvAlbums,
        }),
    },
    watch:{
        'form.groupName':{
            handler(newValue){
                console.log(newValue)
                switch(newValue){
                case 'NCT':
                    this.members = this.nctMembers
                    this.albums = this.nctAlbums
                    break;
                case 'NCT127':
                    this.members = this.nct127Members
                    this.albums = this.nct127Albums
                    break;
                case 'NCT Dream':
                    this.members = this.nctdreamMembers
                    this.albums = this.nctdreamAlbums
                    break;
                case 'WayV':
                    this.members = this.wayvMembers
                    this.albums = this.wayvAlbums
                    break;
            }
            }
        }
    },
    methods:{
        initForm(){
            this.form = {
                groupName: '',
                album: '',
                category: '',
                itemType:'',
                exchangeWay: [],
                ownMember:[],
                targetMember: [],
            }
        }
    }

}
</script>

<style lang="scss" scoped>
.searchExchange {
    padding: 10px 30px;
}
</style>